.nav-cooper{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translatex(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    margin-bottom: 5vh;
}
.nav-cooper button{
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: white;
  font-size: 1.1rem;
}
.nav-cooper button:hover{
  background: rgba(0, 0, 0, 0.3);
}
.nav-cooper button.active{
  background: #e5e6ea;
  color: #1f1f38;
}